    margin: 0 5% 0 5%;
    max-width: 100vw;
}
.wrapper{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    
}

@media (orientation: portrait) {
    .wrapper{
        flex-direction: column;
        
    }
}